.nav-link::after {
content: "";
position: absolute;
bottom: -2px;
left: 0;
width: 0;
height: 2px;
background-color: var(--color-primary);
transition: width 0.3s ease-in-out;
}

.nav-link:hover::after,
.nav-link.active-link::after {
width: 100%;
}


