  .fact-item
  {
    flex: 0 0 25%;
    max-width: 25%;
    margin-top: var(--m-2-5);
    text-align: center;
  }
  .fact-item i
  {
    font-size: var(--h2-font-size);
    color: var(--color-gray);
  }
  
  
  /*========== Portfolio ==========*/
  .portfolio .portfolio-filter
  {
    flex: 0 0 100%;
    max-width: 100%;
    text-align: center;
    /* margin-bottom: var(--m-2-5); */
  }
  .portfolio .portfolio-filter button
  {
    border: none;
    background-color: transparent;
    font-weight: var(--font-semi-bold);
    text-transform: capitalize;
    margin: 0 var(--m-0-75);
    cursor: pointer;
    white-space: nowrap;
    color: var(--color-dark);
    transition: 0.3s;
  }
  .portfolio .portfolio-filter button.active 
  {
    color: var(--color-primary);
    position: relative;
  }
  .portfolio .portfolio-filter button.active::before
  {
    content: '';
    position: absolute;
    bottom: -0.75rem;
    left: 45%;
    width: 0.3rem;
    height: 0.3rem;
    background-color: var(--color-primary);
    border-radius: 50%;
  }
  .portfolio-item
  {
    flex: 0 0 33.33%;
    max-width: 33.33%;
    margin-bottom: var(--m-1-5);
  }
  .portfolio-item.hide
  {
    display: none;
  }
  .portfolio-item.show 
  {
    display: block;
    animation: showItem 0.5s ease;
  }
  @keyframes showItem
  {
    0%
    {
      transform: scale(0.6);
      opacity: 0;
    }
    100%
    {
      transform: scale(1);
      opacity: 1;
    }
  }
  .portfolio-item-inner
  {
    position: relative;
    overflow: hidden;
  }
  .portfolio-item .mask
  {
    background: rgba(0,0,0,.9);
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
    transition: 0.3s;
  }
  .portfolio-item-inner:hover .mask
  {
    opacity: 0.9;
  }
  .portfolio-item .term 
  {
    color: #F6E7D8;
    background: var(--color-primary);
    border-bottom-left-radius: 0.98rem;
    border-bottom-right-radius: 0.98rem;
    position: absolute;
    top: 0;
    left: var(--m-1-5);
    font-size: var(--small-font-size);
    display: inline-block;
    padding: 0.19rem 0.625rem;
    transform: translateY(-40px);
    transition: 0.3s;
  }
  .portfolio-item h3
  {
    color: #F6E7D8;
    font-size: 1.25rem;
    margin: 0 0 0.98rem;
    padding: 0 1.25rem;
    position: absolute;
    top: 3.75rem;
    transition: 0.3s;
    transform: translateY(30px);
    opacity: 0;
  }
  .portfolio-item .more-button
  {
    color: #F6E7D8;
    position: absolute;
    bottom: var(--m-1-5);
    left: var(--m-1-5);
    font-size: 1.6rem;
    /* display: block; */
    background: var(--color-primary);
    height: 2.5rem;
    width: 2.5rem;
    cursor: pointer;
    border-radius: 50%;
    text-align: center;
    line-height: 28px;
    transition: 0.3s;
    opacity: 0;
    
  }
  .more-button {
display: flex;
align-items: center;
justify-content: center;
}

  .portfolio-item-inner:hover h3, .portfolio-item-inner:hover span
  {
    opacity: 1;
    transform: translateY(0);
  }
  
.contact-box {
flex: 0 0 40%;
max-width: 40%;
}

.contact-form {
flex: 0 0 60%;
max-width: 60%;
}

.contact-data {
display: grid;
row-gap: 2rem;
}

.contact-subtitle {
margin-bottom: var(--m-0-5);
}

.contact-description {
display: inline-flex;
align-items: center;
column-gap: .5rem;
}

.contact-icon {
font-size: 1.2rem;
}

.contact-inputs {
display: grid;
row-gap: 2rem;
margin-bottom: var(--m-2-5);
}

.contact-content {
position: relative;
height: 3rem;
border-bottom: 2px solid var(--color-gray);
}

.contact-content span {
position: absolute;
bottom: -2px;
left: 0;
content: '';
height: 2px;
width: 0;
transition: .5s;
background: var(--color-primary);
}

.contact-input:focus ~ span {
width: 100%;
}

.contact-input {
position: absolute;
top: 1rem;
left: 0;
width: 100%;
height: 50%;
padding-bottom: 5px;
background: none;
color: var(--color-primary);
outline: none;
border: none;
z-index: 1;
margin-bottom: 1rem;
}

label {
position: absolute;
top: -2rem;
left: 0;
width: 100%;
font-size: var(--font-medium);
transition: .4s;
pointer-events: none;
color: #F6E7D8;

}

.contact-area {
height: 7rem;
}

.contact-area textarea {
resize: none;
}

.contact-input:focus + label,
.contact-input:not(:placeholder-shown) + label {
top: -.75rem;
z-index: 10;
}


